/* Modal Container */
UserTable {
  border-collapse: collapse;
  width: 100%;
  background-color: aquamarine;
  border-radius: 5px;
  box-shadow: lightskyblue;
  margin-top: 10px;
}

th, td {
  border: 1px solid steelblue;
  padding: 8px;
  text-align:center;
}


.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Modal Content */
  .modal-content {
    background-color: white;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 50%;
    position: relative;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Close Button */
  .close {
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px;
    cursor: pointer;
  }
  
  /* Buttons */
  button {
    position: relative;
    width: 150px;
    height: 40px;
    margin-bottom: 20px;
    background-color: aquamarine;
    color: blue;
    cursor: pointer;
    border-radius: 4px;
  }
  
  /* Button Hover */
  button:hover {
    background-color: grey;
  }
  